'use client';

import { useEffect, useRef, useCallback } from 'react';
import { OrbitControls } from '@react-three/drei';
import { useThree } from '@react-three/fiber';

export default function PassiveOrbitControls(props) {
    const controlsRef = useRef();
    const rafRef = useRef(null);
    const runningRef = useRef(false);
    const { invalidate, gl } = useThree();

    useEffect(() => {
        if (gl && gl.domElement) {
            gl.setPixelRatio(Math.min(window.devicePixelRatio, 1.5));
        }
    }, [gl]);

    const handleControlsRef = useCallback((node) => {
        controlsRef.current = node;
        if (node) {
            node.enableZoom = props.enableZoom !== false;
            node.enablePan = props.enablePan !== false;
            if (node.mouseButtons && props.enableZoom === false) {
                node.mouseButtons.WHEEL = null;
            }
        }
    }, [props.enableZoom, props.enablePan]);

    useEffect(() => {
        const controls = controlsRef.current;
        if (!controls) return;

        const handleChange = () => {
            invalidate();
        };

        controls.addEventListener('change', handleChange);

        if (props.autoRotate) {
            runningRef.current = true;
            let lastTime = performance.now();
            const targetFPS = 30;
            const interval = 1000 / targetFPS;

            const tick = (currentTime) => {
                if (!runningRef.current || !controls || !controls.object) return;

                const delta = currentTime - lastTime;
                if (delta >= interval) {
                    if (controls.autoRotate) {
                        controls.update();
                        invalidate();
                    }
                    lastTime = currentTime - (delta % interval);
                }

                rafRef.current = requestAnimationFrame(tick);
            };

            rafRef.current = requestAnimationFrame(tick);

            return () => {
                runningRef.current = false;
                if (rafRef.current) {
                    cancelAnimationFrame(rafRef.current);
                    rafRef.current = null;
                }
                controls.removeEventListener('change', handleChange);
            };
        }

        return () => {
            runningRef.current = false;
            if (rafRef.current) {
                cancelAnimationFrame(rafRef.current);
                rafRef.current = null;
            }
            controls.removeEventListener('change', handleChange);
        };
    }, [invalidate, props.autoRotate]);

    return <OrbitControls
        ref={handleControlsRef}
        {...props}
        enableZoom={props.enableZoom !== false}
        enablePan={props.enablePan !== false}
    />;
}
