import * as THREE from "three";

export function setupMaterialTextures(material) {
  if (!material || !material.isMaterial) return;

  const textureMaps = [
    'map',
    'normalMap',
    'roughnessMap',
    'metalnessMap',
    'aoMap',
    'emissiveMap',
    'bumpMap',
    'displacementMap',
    'alphaMap',
    'clearcoatMap',
    'clearcoatNormalMap',
    'clearcoatRoughnessMap',
    'iridescenceMap',
    'iridescenceThicknessMap',
    'sheenColorMap',
    'sheenRoughnessMap',
    'specularColorMap',
    'specularIntensityMap',
    'specularIntensityMap',
    'transmissionMap',
    'thicknessMap',
  ];

  textureMaps.forEach((mapName) => {
    const texture = material[mapName];
    if (texture && texture.isTexture) {
      texture.flipY = false;
      texture.wrapS = THREE.RepeatWrapping;
      texture.wrapT = THREE.RepeatWrapping;
      
      if (mapName === 'normalMap' || mapName.includes('Normal')) {
        texture.colorSpace = THREE.NoColorSpace;
      } else if (mapName === 'map' || mapName === 'emissiveMap' || mapName === 'aoMap') {
        texture.colorSpace = THREE.SRGBColorSpace;
      } else {
        texture.colorSpace = THREE.NoColorSpace;
      }
      
      texture.needsUpdate = true;
    }
  });

  material.needsUpdate = true;
}

export function setupGLTFMaterials(materials) {
  if (!materials) return;

  Object.values(materials).forEach((material) => {
    if (material && material.isMaterial) {
      setupMaterialTextures(material);
    }
  });
}
